.App {
  background-color: rgba(43,39,125);
  background-image: linear-gradient(to bottom, #23214d, #27616d);
}
.icon-wpp-button {
  position:fixed;
  width:60px;
  height:60px;
  bottom:40px;
  right:40px;
  background-color:#25d366;
  color:#FFF;
  border-radius:50px;
  text-align:center;
  font-size:40px;
  z-index:100;
}

.wpp-icon {
  margin-bottom: 1rem;
  color: white;
}

.navbar-bkg {
  background-color: rgb(0,0,0);
}
.effect-button{
  background-color: transparent;
  transition: 0.77s !important;
}

.effect-button:hover{
  background-color: #242851;
  transition: 0.77s;
}
.main-container {
  margin-top: 2rem;
}
.video-class {
  height: 100%;
  width: 100%;
}
.about-content {
  text-align: justify;
  background-color: rgba(0,0,0, 0.6);
  margin-top: 2rem;
  padding: 1rem 9rem 1rem 9rem !important;
  color: white;
}
.under-title {
  width: 50%;
  margin: 0 auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  color: white;
}
.titles {
  text-align: center;
  font-size: 1.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: white;
}
.icon-animation{
  -webkit-animation: pulse 0.7s infinite;
          animation: pulse 0.7s infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-filter: brightness(200%);
            filter: brightness(200%);
    transition: 0.5s;
    color: yellow;
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
  }
  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-filter: brightness(200%);
            filter: brightness(200%);
    transition: 0.5s;
    color: yellow;
  }
}

@media only screen and (max-width: 767px) {
  .about-content {
    margin-top: 2rem;
    padding: 1rem!important;
    color: white;
  }
  .icon-animation{
    width: 2rem;
    height: 2rem;
  }
}

.footer-bkg {
  background-color: black;
  color: white;
}

.footer{
  color: #C6C2C1;
  margin: 0 auto;
}
.navbar-bkg{
  background-color: rgb(0,0,0);
}
.social-buttons{
  height: 2em;
  width: 2em;
  margin-right: 1rem;
  margin-bottom: 1rem;
}
.title{
  text-align: center;
  font-size: 1.5rem;
  color: white;
}
a{
  text-decoration: none !important;
  color: white !important;
}
.social-content{
  display: flex;
  justify-content: center;
  margin: 0 auto;
}
.card-total {
  background-color: transparent !important;
  max-height: 100%;
}
.bkg-card{
  background-color: rgba(0,0,0, 0.9);
  transition: 1s;
  color: white;
}
.bkg-card:hover{
  transition: 0.77s;
  background-color: #d39d88;
  color: black;
}

